import React from 'react'
import ReactMarkdown from 'react-markdown'
import people from '../content/output/people.json'

const faculties = people.filter((person) => {
  return person.type === 'faculty'
})
const phds = people.filter((person) => {
  return person.type === 'phd'
})
const masters = people.filter((person) => {
  return person.type === 'master'
})
const undergrads = people.filter((person) => {
  return person.type === 'undergrad'
})
const alums = people.filter((person) => {
  return person.type === 'alum'
})


class People extends React.Component {
  componentDidMount() {
  }

  render() {
    return (
      <div id="people" >
        <h1>People</h1>
        <h2>Faculty</h2>
        <div class="ui grid">
          { faculties.map((person) => {
            return (
              <div class="four wide column">
                <img class="ui medium circular image" src="/images/wireframe/square-image.png"/>
                <p>{ person.name }</p>
              </div>
            )
          })}
        </div>
      </div>
    )
  }
}

export default People
